import * as React from 'react';
import './index.scss';
import homeImg from '../../images/kelehualao.gif';

export default class HeaderBar extends React.Component<{}, {}>{
    constructor(props: {}) {
        super(props)
    };

    public render() {
        return (
            <div style={{ textAlign: 'center', marginTop: 100 }}>
                <img src={homeImg} />
            </div>
        )
    }
}


